import React, { FC, useEffect, useMemo, useState } from 'react';
import * as ST from './styled';
import {
  AppLoader,
  Avatar,
  AvatarSize,
  Button,
  ButtonVariant,
  CmSelectValue,
  InfoItem, Input,
  Role,
  Select,
  SideModal,
  Status, Textarea
} from '@src/components';
import { IUser, UserStatus } from '@src/api/Users/models';
import { useModalControls } from '@src/hooks';
import { Dialog, DialogTypes } from '@components/Dialog';
import { UsersApi } from '@src/api/Users/UsersApi';
import { handleFormError, toastError, toastSuccess } from '@src/utils/toast';
import { getTime } from '@src/utils/date';
import { useFormik } from 'formik';
import { SelectOption } from '@components/Select/hooks/useSearch';
import { RolesApi } from '@src/api/Roles/RolesApi';
import { useRenderIfRole } from '@src/utils/roles';
import { RoleType } from '@src/api/Roles/models';
import { Icon } from '@components/Icon';
import { useDialogsContext } from '@providers/DialogsProvider/DialogsContext';

interface IProps {
  userId: string;
  onClose: () => void;
  refetch?: () => void;
}

interface IForm {
  roles: SelectOption<string>[];
}

export const UserModal: FC<IProps> = ({onClose, refetch, userId }) => {
  const [user, setUser] = useState<IUser>();
  const { createDialog } = useDialogsContext();
  const [isEdit, setIsEdit] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isChangeLoading, setIsChangeLoading] = useState(false);
  const [banReason, setBanReason] = useState('');
  const [message, setMessage] = useState('');
  const banModal = useModalControls();
  const newMessageModal = useModalControls();
  const [isRolesLoading, setIsRolesLoading] = useState(true);
  const [roleOptions, setRoleOptions] = useState<SelectOption<string>[]>([]);
  const { execIf } = useRenderIfRole();

  const form = useFormik<IForm>({
    initialValues: {
      roles: user?.roles.map(it => ({ value: it.id, label: it.value })) || []
    },
    onSubmit: async (values, { resetForm }) => {
      try {
        setIsChangeLoading(true);
        await UsersApi.changeRoles({ roles: values.roles.map(it => it.value), userId: userId });
        refetch?.();
        setIsEdit(false);
        resetForm();
        toastSuccess('  ');
      } catch (e) {
        toastError(e);
      } finally {
        setIsChangeLoading(false);
      }
    }
  });

  const isActive = user?.status === UserStatus.ACTIVE;

  const getUser = async () => {
    try {
      setIsLoading(true);
      const res = await UsersApi.fetchUser(userId);
      await form.setFieldValue('roles', res.data?.roles.map(it => ({ value: it.id, label: it.value })))

      setUser(res.data)
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  const handleConfirmCreateDialog = () => {
    createDialog({ userId: userId, text: message });
  }

  const handleBlockUnBlock = async () => {
    try {
      setIsLoading(true);
      if (isActive) {
        const res = await UsersApi.banUser({ id: userId, text: banReason });
        toastSuccess(res.data.message);
      } else {
        const res = await UsersApi.unbanUser(userId)
        toastSuccess(res.data.message);
      }
      refetch?.()
      banModal.close();
      onClose();
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  const loadRoles = async () => {
    try {
      setIsRolesLoading(true);
      const res = await RolesApi.fetchRoles();
      setRoleOptions(res.data.map(it => ({ value: it.id, label: it.value })));
    } catch (e) {
      handleFormError(e, form);
    } finally {
      setIsRolesLoading(false);
    }
  }

  const handleChangeRoles = (value: CmSelectValue) => {
    form.setFieldValue('roles', value);
  }

  useEffect(() => {
    loadRoles()
    getUser()
  }, []);

  const handleCloseEdit = () => {
    setIsEdit(false);
    form.resetForm();
  }

  const handleOpenEdit = () => {
    setIsEdit(true);
    form.setFieldValue('roles', user?.roles.map(it => ({ value: it.id, label: it.value })))
  }

  const renderedContent = useMemo(() => {
    if (isLoading || !user) {
      return <AppLoader />
    }

    if (isEdit) {
      return (
        <ST.Form onSubmit={form.handleSubmit}>
          <Select
            placeholder=" "
            caption=""
            multi
            options={roleOptions}
            loading={isRolesLoading}
            value={form.values.roles}
            onChange={handleChangeRoles}
          />
          <ST.Footer>
            <Button text="" fullWidth isLoading={isChangeLoading} />
            <Button text="" variant={ButtonVariant.OUTLINE} fullWidth onClick={handleCloseEdit} />
          </ST.Footer>
        </ST.Form>
      )
    }

    return (
      <>
        <ST.HeaderInfo>
          <Avatar
            userId={user?.id}
            firstName={user?.firstName}
            lastName={user?.lastName}
            src={user?.avatar}
            size={AvatarSize.EXTRA}
            figure={'square'}
          />
          <ST.Name>{user?.lastName} {user?.firstName}</ST.Name>
          <Status status={user?.status}/>
        </ST.HeaderInfo>
        <InfoItem
          text="ID:"
          info={user?.id}
        />
        <InfoItem
          text=":"
          info={(
            <ST.Roles>
              {user?.roles.map(it => (
                <Role key={it.id} role={it.value}/>
              ))}
            </ST.Roles>
          )}
        />
        <InfoItem
          text=":"
          info={<ST.Email>{user?.email}</ST.Email>}
        />
        <InfoItem
          text=" :"
          info={<Status status={user?.isConfirmedEmail ? UserStatus.ACTIVE : UserStatus.BANNED}>{user?.isConfirmedEmail ? '' : ' '}</Status>}
        />
        <InfoItem
          text=":"
          info={getTime(user?.createdAt)}
        />
        <InfoItem
          text=":"
          info={getTime(user?.updatedAt)}
        />
        {user.banReason && (
          <InfoItem
            text=" :"
            info={(
              <ST.BanInfoWrapper>
                {user.banReason.text}
              </ST.BanInfoWrapper>
            )}
          />
        )}
        {user.banReason && (
          <InfoItem
            text=" :"
            info={(
              <>
                <Avatar
                  firstName={user.banReason.user.firstName}
                  lastName={user.banReason.user.lastName}
                  src={user.banReason.user.avatar}
                />
                {user.banReason.user.lastName} {user.banReason.user.firstName} ({user.banReason.user.email})
              </>
            )}
          />
        )}
        <ST.Footer>
          {execIf(RoleType.ADMIN, <Button text=" " fullWidth onClick={handleOpenEdit}/>)}
          <Button
            fullWidth
            onClick={banModal.open}
            variant={isActive ? ButtonVariant.DANGER : ButtonVariant.FILLED}
            text={isActive ? '' : ''}
          />
        </ST.Footer>
      </>
    )
  }, [isEdit, isActive, user, isChangeLoading, form, isRolesLoading, roleOptions, isLoading]);

  return (
    <SideModal
      titleText={(
        <ST.Header>
          <ST.Title></ST.Title>
          <Button
            text=" "
            onClick={newMessageModal.open}
          />
        </ST.Header>
      )}
      onClose={onClose}
      closeOnDocumentClick={false}
    >
      <ST.Wrapper>
        {renderedContent}
      </ST.Wrapper>
      {banModal.isOpen && (
        <Dialog
          type={isActive ? DialogTypes.WARNING : DialogTypes.ANSWER}
          closeHandler={banModal.close}
          onConfirm={handleBlockUnBlock}
          actions={{
            cancel: {
              text: '',
              onClick: banModal.close,
            },
            submit: {
              isLoading,
              text: isActive ? '' : '',
            }
          }}
        >
          <ST.BanWrapper>
            <ST.BanText>
                 {isActive ? '' : ''}  {user?.lastName} {user?.firstName} ({user?.email})?
            </ST.BanText>
            {isActive && <Input value={banReason} onChange={e => setBanReason(e.target.value)} placeholder=" " />}
          </ST.BanWrapper>
        </Dialog>
      )}
      {newMessageModal.isOpen && (
        <Dialog
          type={DialogTypes.INFO}
          closeHandler={newMessageModal.close}
          onConfirm={handleConfirmCreateDialog}
          title=" "
          actions={{
            cancel: {
              text: '',
              onClick: newMessageModal.close,
            },
            submit: {
              isLoading,
              text: ' ',
            }
          }}
        >
          <ST.StyledTextArea placeholder=" " value={message} onChange={(e) => setMessage(e.target.value)} />
        </Dialog>
      )}
    </SideModal>
  )
}
