import { FC, useMemo, useState } from 'react';
import * as ST from './styled';
import { AppLoader, Button, ButtonVariant, PageHeader, Select } from '@src/components';
import { ComplaintItem } from '@pages/Complaints/ComplaintItem';
import { ComplaintModal } from '@pages/Complaints/ComplaintModal';
import { useFilters } from '@pages/Complaints/useFilters';
import { IComplaint } from '@src/api/Complaints/models';

const Complaints: FC = () => {
  const {
    form,
    getComplaints,
    complaints,
    typesOptions,
    usersOptions,
    usersRes,
    createSelectChangeHandler,
    isLoading,
    statusOptions,
    sortOptions,
    reset,
    setActiveComplaint,
    activeComplaint,
  } = useFilters();

  const renderedComplaints = useMemo(() => {
    if (isLoading) {
      return (
        <AppLoader />
      )
    }

    if (!complaints.length) {
      return (
        <ST.EmptyText>
           
        </ST.EmptyText>
      );
    }

    return (
      <ST.Content>
        {complaints.map(it => <ComplaintItem key={it.id} complaint={it} onClick={() => setActiveComplaint(it)} />)}
      </ST.Content>
    )
  }, [complaints, isLoading]);

  return (
    <ST.Wrapper>
      <PageHeader
        title=""
        count={`${complaints.length} `}
      />
      <ST.Filters>
        <Select value={form.values.types} options={typesOptions} onChange={createSelectChangeHandler('types')} placeholder="   " />
        <Select value={form.values.users} options={usersOptions} loading={usersRes.isLoading} onChange={createSelectChangeHandler('users')} placeholder=" " />
        <Select value={form.values.status} options={statusOptions} onChange={createSelectChangeHandler('status')} placeholder=" " />
        <Select value={form.values.sort} options={sortOptions} onChange={createSelectChangeHandler('sort')} placeholder=" " />
        <Button text="" onClick={reset} />
      </ST.Filters>
      {renderedComplaints}
      {activeComplaint && <ComplaintModal complaintId={activeComplaint.id} onClose={() => setActiveComplaint(undefined)} refetch={getComplaints} />}
    </ST.Wrapper>
  )
}

export default Complaints;
