import { IUser, UserStatus } from '@src/api/Users/models';
import { ITag } from '@src/api/Tags/models';

export interface IPost {
  id: string;
  title: string;
  videoUrl: string;
  description: string;
  shortDescription: string;
  status: UserStatus;
  user: IUser;
  tags: ITag[];
  updatedAt: string;
  createdAt: string;
  views: number;
  commentsCount: number;
  favoriteCount: number;
}

export interface IComment {
  id: string;
  text: string;
  createdAt: string;
  updatedAt: string;
  user: IUser;
}

export enum PostsSort {
  CREATED_AT = 'createdAt',
  VIEWS = 'views',
}

export enum SortValue {
  DESC = 'desc',
  ASC = 'asc'
}

export interface IBanReason {
  text: string;
  user: IUser;
}

export interface IPostDetail extends IPost {
  comments: IComment[];
  banReason?: IBanReason;
}

export interface IPostBanDTO {
  id: string;
  text: string;
}

export interface IGetPostsParams {
  search?: string;
  tags?: string[];
  users?: string[];
  status?: string[];
  sortValue?: string;
  sort?: string;
}
