import styled from 'styled-components';
import { COLORS } from '@src/utils/colors';
import ReactPlayer from 'react-player';
import { Icon } from '@components/Icon';

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  grid-row-gap: 18px;
  width: 100%;
  min-height: 100%;
  height: fit-content;
`;

export const Header = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  width: 100%;
`;

export const Title = styled.div`
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.014em;
`;

export const Roles = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
`;

export const Info = styled.div`
  display: flex;
  width: 100%;
  gap: 10px;
  align-items: center;
`;

export const Footer = styled.div`
  margin-top: auto;
  display: flex;
  align-items: center;
  grid-column-gap: 24px;
`;

export const ComplaintTarget = styled.div`
  display: flex;
  align-items: center;
  padding: 5px;
  background-color: ${COLORS.lightGray20};
  cursor: pointer;
  border-radius: 5px;
  transition: background-color .3s;
  
  &:hover {
    background-color: ${COLORS.lightGray50};
  }
`;

export const ScrollableContent = styled.div`
  width: 100%;
  height: 100%;
  overflow-y: auto;
  position: relative;

  &::-webkit-scrollbar {
    width: 0;
  }
`;

export const NewMessage = styled.div`
  position: absolute;
  cursor: pointer;
  bottom: 76px;
  left: calc(50% - 69px);
  display: flex;
  padding: 4px 8px;
  border-radius: 5px;
  color: ${COLORS.lightGray10};
  background-color: ${COLORS.blue60};
  
  &:hover {
    background-color: ${COLORS.blue70};
  }

  &:active,
  &.active {
    background-color: ${COLORS.blue80};
  }
`;

export const Chat = styled.div`
  display: flex;
  position: relative;
  flex-direction: column;
  width: 100%;
  min-height: 500px;
  height: 100%;
  max-height: 600px;
  justify-content: flex-end;
  background-color: ${COLORS.lightGray10};
  border-radius: 8px;
`;

export const ChatContent = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 10px;
  width: 100%;
  height: fit-content;
  min-height: 100%;
  padding: 10px;
`;

export const ChatFooter = styled.form`
  display: flex;
  width: 100%;
  height: fit-content;
  gap: 10px;
  align-items: center;
  padding: 10px;
  border-top: 1px solid ${COLORS.lightGray40};
`;

export const StyledButton = styled.button`
  background: transparent;
`;

export const SendIcon = styled(Icon)`
  cursor: pointer;
  color: ${COLORS.gray60};
  transition: color .3s;
  
  &:hover {
    color: ${COLORS.blue60};
  }
`;

export const InfoItem = styled.div`
  display: flex;
  color: ${COLORS.gray70};
  width: fit-content;
  gap: 6px;
  align-items: center;
`;

export const Description = styled.div`
  font-size: 14px;
  color: ${COLORS.gray90};
`;
