import styled from 'styled-components';
import { COLORS } from '@src/utils/colors';

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid ${COLORS.lightGray40};
  background: ${COLORS.white};
  cursor: pointer;
  transition: background-color .3s;
  border: 1px solid ${COLORS.lightGray40};

  &:hover {
    background: ${COLORS.lightGray20};
  }
`;

export const Header = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
`;

export const Info = styled.div`
  display: flex;
  width: fit-content;
  gap: 10px;
  align-items: center;
`;

export const InfoItem = styled.div`
  display: flex;
  color: ${COLORS.gray70};
  width: fit-content;
  gap: 6px;
  align-items: center;
`;

export const Title = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 15px;
  font-weight: 500;
`;

export const MessagesCount = styled.div`
  display: flex;
  padding: 5px;
  border-radius: 5px;
  font-size: 12px;
  color: ${COLORS.white};
  background-color: ${COLORS.blue60};
`;

export const Description = styled.div`
  font-size: 14px;
  color: ${COLORS.gray90};
`;
