import { ITag } from '@src/api/Tags/models';
import { FC } from 'react';
import * as ST from './styled';
import { AVATAR_COLORS_SUBTLE } from '@components/Avatar/AvatarColors';
import { getRadixTransformation } from '@src/components';

interface IProps {
  tag: ITag;
}

export const Tag: FC<IProps> = ({ tag }) => {
  return (
    <ST.Wrapper style={AVATAR_COLORS_SUBTLE[
      getRadixTransformation(tag.id.slice(-1), AVATAR_COLORS_SUBTLE.length)
      ]}>
      {tag.value}
    </ST.Wrapper>
  );
}
