import styled from 'styled-components';
import { COLORS } from '@src/utils/colors';
import { Avatar, Textarea } from '@src/components';
import { Icon } from '@components/Icon';

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  grid-row-gap: 18px;
  width: 100%;
  min-height: 100%;
  height: fit-content;
`;

export const StyledTextArea = styled(Textarea)`
  width: 100%;
  text-align: left;
`;

export const Form = styled.form`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  grid-row-gap: 24px;
`;

export const BanWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: left;
`;

export const BanText = styled.div`
  text-align: center;
`;

export const Header = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  width: 100%;
`;

export const Title = styled.div`
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.014em;
`;

export const HeaderInfo = styled.div`
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  grid-gap: 10px;
`;

export const Roles = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
`;

export const Email = styled.div`
  display: flex;
  color: ${COLORS.gray90};
    padding: 5px;
    border-radius: 5px;
    background-color: ${COLORS.lightGray10};
`;

export const Name = styled.div`
  font-size: 24px;
`;

export const Footer = styled.div`
  margin-top: auto;
  display: flex;
  align-items: center;
  grid-column-gap: 24px;
`;

export const BanInfoWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 6px;
  padding: 6px;
  color: ${COLORS.red90};
  background-color: ${COLORS.red30};
`;
