import { RoleType } from '@src/api/Roles/models';
import { useAuth } from '@providers/AuthProvider';

export const useRenderIfRole = () => {
  const { user } = useAuth();

  const execIf = (role: RoleType, content: any) => {
    if (!user?.roles.map(it => it.value).includes(role)) {
      return null;
    }

    return content;
  }

  return {
    execIf
  }
}

