import { instance } from '@src/api/base';
import { IComplaint, IComplaintDetails, IGetComplaintsParams } from './models';

export class ComplaintsApi {
  static fetchComplaints(params: IGetComplaintsParams) {
    return instance.get<IComplaint[]>('/complaints', {
      params,
    });
  }

  static fetchResolveComplaint(id: string) {
    return instance.post(`/complaints/resolve/${id}`);
  }

  static fetchComplaint(id: string) {
    return instance.get<IComplaintDetails>(`/complaints/${id}`);
  }

  static fetchCloseComplaint(id: string) {
    return instance.post(`/complaints/close/${id}`);
  }

  static fetchUnresolveComplaint(id: string) {
    return instance.post(`/complaints/unresolve/${id}`);
  }
}
