import styled from 'styled-components';
import { COLORS } from '@src/utils/colors';
import ReactPlayer from 'react-player';

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  grid-row-gap: 18px;
  width: 100%;
  min-height: 100%;
  height: fit-content;
`;

export const Header = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  width: 100%;
`;

export const BanWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: left;
`;

export const BanText = styled.div`
  text-align: center;
`;

export const Title = styled.div`
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.014em;
`;

export const Roles = styled.div`
  display: flex;
  align-items: center;
  gap: 10px;
`;

export const Name = styled.div`
  font-size: 24px;
`;

export const Info = styled.div`
  display: flex;
  width: 100%;
  gap: 10px;
  align-items: center;
`;

export const Footer = styled.div`
  margin-top: auto;
  display: flex;
  align-items: center;
  grid-column-gap: 24px;
`;

export const Video = styled(ReactPlayer)`
  width: 100% !important;
`

export const CommentsText = styled.div`
  font-size: 17px;
  padding-bottom: 5px;
  border-bottom: 1px solid ${COLORS.lightGray40};
`

export const Comments = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

export const InfoItem = styled.div`
  display: flex;
  color: ${COLORS.gray70};
  width: fit-content;
  gap: 6px;
  align-items: center;
`;



export const Description = styled.div`
  font-size: 14px;
  color: ${COLORS.gray90};
`;

export const BanInfoWrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
  border-radius: 6px;
  padding: 6px;
  color: ${COLORS.red90};
  background-color: ${COLORS.red30};
`;
