import { FC, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import * as ST from './styled';
import {
  AppLoader,
  Avatar,
  Button,
  ButtonVariant,
  InfoItem,
  SideModal,
  StatusComplaint, Textarea,
  TextShorter,
  TypeComplaint
} from '@src/components';
import { Dialog, DialogTypes } from '@components/Dialog';
import { toastError, toastSuccess } from '@src/utils/toast';
import { getTime } from '@src/utils/date';
import { ComplaintStatus, ComplaintType, IComplaintDetails, IComplaintMessage } from '@src/api/Complaints/models';
import { ComplaintsApi } from '@src/api/Complaints/ComplaintsApi';
import { useMessageCallBack, useModalControls } from '@src/hooks';
import { UserModal } from '@pages/Users/UserModal';
import { PostModal } from '@pages/Posts/PostModal';
import { IconSize } from '@components/Icon';
import { useFormik } from 'formik';
import { SocketApi } from '@src/api/SocketApi/SocketApi';
import { IDeleteMessage, ISocketMessage } from '@src/api/Dialogs/models';
import { MessageEvents } from '@src/api/SocketApi/models';
import { useAuth } from '@providers/AuthProvider';
import { Message } from '@pages/Dialogs/Message';

interface IProps {
  complaintId: string;
  onClose: () => void;
  refetch: () => void;
}

export const ComplaintModal: FC<IProps> = ({onClose, refetch, complaintId}) => {
  const [complaint, setComplaint] = useState<IComplaintDetails>();
  const [messages, setMessages] = useState<IComplaintMessage[]>([]);
  const { user: currentUser } = useAuth();
  const [isLoading, setIsLoading] = useState(false);
  const [isInitialLoading, setIsInitialLoading] = useState(false);
  const closeModal = useModalControls();
  const resolveModal = useModalControls();
  const unresolveModal = useModalControls();
  const [userId, setUserId] = useState<string>();
  const [postId, setPostId] = useState<string>();
  const [showNewMessage, setShowNewMessage] = useState(false);
  const chatRef = useRef<HTMLDivElement | null>(null);

  const scrollToBottom = () => {
    chatRef.current?.scrollTo(0, chatRef.current?.scrollHeight)
  }

  const form = useFormik({
    initialValues: {
      message: ''
    },
    onSubmit: (values) => {
      onSend(values.message)
    }
  });

  const getComplaint = async () => {
    try {
      setIsInitialLoading(true);
      const res = await ComplaintsApi.fetchComplaint(complaintId);

      setComplaint(res.data);
      setMessages(res.data.messages);
    } catch (e) {
      toastError(e)
    } finally {
      setIsInitialLoading(false);
    }
  }

  const handleScroll = () => {
    if (chatRef.current) {
      const scroll = chatRef.current?.scrollHeight - chatRef.current?.clientHeight - chatRef.current?.scrollTop

      if (scroll < 25) {
        setShowNewMessage(false);
      }
    }
  }

  const handleResolve = async () => {
    try {
      setIsLoading(true);
      const res = await ComplaintsApi.fetchResolveComplaint(complaintId);
      toastSuccess(res.data.message);
      refetch();
      resolveModal.close();
      onClose();
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  const handleClose = async () => {
    try {
      setIsLoading(true);
      const res = await ComplaintsApi.fetchCloseComplaint(complaintId);
      toastSuccess(res.data.message);
      refetch();
      closeModal.close();
      onClose();
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  const handleUnresovle = async () => {
    try {
      setIsLoading(true);
      const res = await ComplaintsApi.fetchUnresolveComplaint(complaintId);
      toastSuccess(res.data.message);
      refetch();
      unresolveModal.close();
      onClose();
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  const handleOpenUserModal = () => {
    setUserId(complaint?.user?.id);
  }

  const handleOpenPostModal = () => {
    setPostId(complaint?.post?.id);
  }

  const handleCloseUserModal = () => {
    setUserId(undefined);
  }

  const handleClosePostModal = () => {
    setPostId(undefined);
  }

  const onSend = (message: string) => {
    SocketApi.sendComplaintMessage({
      complaintId,
      text: message,
    });
    form.resetForm();
  }

  useEffect(() => {
    getComplaint();
  }, [complaintId]);

  useMessageCallBack((message: string) => {
    const parsedMessage = JSON.parse(message) as IComplaintMessage;

    if (parsedMessage.event === MessageEvents.COMPLAINT_MESSAGE && parsedMessage.complaintId === complaintId) {
      setMessages(prev => [...prev, parsedMessage]);

      if (parsedMessage.user.id !== currentUser?.id) {
        SocketApi.readMessage({ messageId: parsedMessage.id });
        if (chatRef.current) {
          const scroll = chatRef.current?.scrollHeight - chatRef.current?.clientHeight - chatRef.current?.scrollTop
          if (scroll >= 50) {
            setShowNewMessage(true)
          }
        }
      }
    }
  }, [complaintId, currentUser]);

  useLayoutEffect(() => {
    if (chatRef.current) {
      const lastMsg = messages[messages.length - 1];
      const scroll = chatRef.current?.scrollHeight - chatRef.current?.clientHeight - chatRef.current?.scrollTop;

      if (lastMsg && lastMsg.user.id === currentUser?.id) {
        scrollToBottom()
      } else if (scroll < 50) {
        scrollToBottom()
      }
    }

  }, [messages]);

  const renderedContent = useMemo(() => {
    if (!messages.length) {
      return ' '
    }

    return messages.map((it) => <Message key={it.id} message={it} withAvatar />)
  }, [messages]);

  return (
    <SideModal
      titleText={complaint && (
        <ST.Header>
          <ST.Title>
            <TextShorter tooltip>
              # {complaint.id}
            </TextShorter>
          </ST.Title>
          <TypeComplaint type={complaint.type} />
          <StatusComplaint status={complaint.status}/>
        </ST.Header>
      )}
      onClose={onClose}
      closeOnDocumentClick={false}
    >
      <ST.Wrapper>
        {isInitialLoading || !complaint ? (
          <AppLoader />
        ) : (
          <>
            <InfoItem
              text="ID:"
              info={(
                <>
                  {complaint.id}
                </>
              )}
            />
            <InfoItem
              text=":"
              info={(
                <>
                  {complaint.text}
                </>
              )}
            />
            <InfoItem
              text=":"
              info={(
                <>
                  <Avatar
                    firstName={complaint.author.firstName}
                    lastName={complaint.author.lastName}
                    src={complaint.author.avatar}
                    userId={complaint.author.id}
                  />
                  {complaint.author.lastName} {complaint.author.firstName} ({complaint.author.email})
                </>
              )}
            />
            <InfoItem
              text={`  ${complaint.type === ComplaintType.POST ? '' : ''}:`}
              info={complaint.type === ComplaintType.POST ? (
                <ST.ComplaintTarget onClick={handleOpenPostModal}>
                  {complaint.post.title} ({complaint.post.id})
                </ST.ComplaintTarget>
              ) : (
                <ST.ComplaintTarget onClick={handleOpenUserModal}>
                  <Avatar
                    firstName={complaint.user.firstName}
                    lastName={complaint.user.lastName}
                    src={complaint.user.avatar}
                    userId={complaint.user.id}
                  />
                  {complaint.user.lastName} {complaint.user.firstName} ({complaint.user.email})
                </ST.ComplaintTarget>
              )}
            />
            <InfoItem
              text=":"
              info={getTime(complaint.createdAt)}
            />
            <InfoItem
              text=":"
              info={getTime(complaint.updatedAt)}
            />
            {complaint.whoResolve && (
              <InfoItem
                text={` ${complaint.status === ComplaintStatus.RESOLVED ? '' : ''}:`}
                info={(
                  <>
                    <Avatar
                      firstName={complaint.whoResolve.firstName}
                      lastName={complaint.whoResolve.lastName}
                      src={complaint.whoResolve.avatar}
                      userId={complaint.whoResolve.id}
                    />
                    {complaint.whoResolve.lastName} {complaint.whoResolve.firstName} ({complaint.whoResolve.email})
                  </>
                )}
              />
            )}
            <ST.Chat>
              <ST.ScrollableContent ref={chatRef} onScroll={handleScroll}>
                <ST.ChatContent>
                  {renderedContent}
                </ST.ChatContent>
              </ST.ScrollableContent>
              {showNewMessage && (
                <ST.NewMessage onClick={scrollToBottom}>
                   
                </ST.NewMessage>
              )}
              <ST.ChatFooter onSubmit={form.handleSubmit}>
                <Textarea
                  name="message"
                  value={form.values.message}
                  onChange={form.handleChange}
                  placeholder=" "
                  onEnter={() => onSend(form.values.message)}
                />
                <ST.StyledButton>
                  <ST.SendIcon type="send" size={IconSize.XL} />
                </ST.StyledButton>
              </ST.ChatFooter>
            </ST.Chat>
            <ST.Footer>
              {complaint.status === ComplaintStatus.RESOLVED || complaint.status === ComplaintStatus.CLOSED ? (
                <Button fullWidth text="" onClick={unresolveModal.open} />
              ) : (
                <>
                  <Button
                    fullWidth
                    onClick={resolveModal.open}
                    text=""
                  />
                  <Button
                    fullWidth
                    onClick={closeModal.open}
                    variant={ButtonVariant.DANGER}
                    text=""
                  />
                </>
              )}

            </ST.Footer>
          </>
        )}
      </ST.Wrapper>
      {
        resolveModal.isOpen && (
          <Dialog
            type={DialogTypes.INFO}
            closeHandler={resolveModal.close}
            onConfirm={handleResolve}
            actions={{
              cancel: {
                text: '',
                onClick: resolveModal.close,
              },
              submit: {
                isLoading,
                text: '',
              }
            }}
          >
                 "{complaintId}" ?
          </Dialog>
        )
      }
      {
        closeModal.isOpen && (
          <Dialog
            type={DialogTypes.WARNING}
            closeHandler={closeModal.close}
            onConfirm={handleClose}
            actions={{
              cancel: {
                text: '',
                onClick: closeModal.close,
              },
              submit: {
                isLoading,
                text: '',
              }
            }}
          >
                 "{complaintId}"?
          </Dialog>
        )
      }
      {
        unresolveModal.isOpen && (
          <Dialog
            type={DialogTypes.WARNING}
            closeHandler={unresolveModal.close}
            onConfirm={handleUnresovle}
            actions={{
              cancel: {
                text: '',
                onClick: unresolveModal.close,
              },
              submit: {
                isLoading,
                text: '',
              }
            }}
          >
                 "{complaintId}"?
          </Dialog>
        )
      }
      {userId && (
        <UserModal userId={userId} onClose={handleCloseUserModal} />
      )}
      {postId && (
        <PostModal postId={postId} onClose={handleClosePostModal} />
      )}
    </SideModal>
  )
}
