import { useFormik } from 'formik';
import { useDebounce, useMessageCallBack, useTags, useUsers } from '@src/hooks';
import { useEffect, useMemo, useState } from 'react';
import { SelectOption } from '@components/Select/hooks/useSearch';
import { CmSelectValue, COMPLAINT_TYPE_LABELS } from '@src/components';
import { toastError } from '@src/utils/toast';
import { SortValue } from '@src/api/Posts/models';
import { ComplaintsApi } from '@src/api/Complaints/ComplaintsApi';
import {
  ComplaintsSort,
  ComplaintStatus,
  ComplaintType,
  IComplaint,
  IComplaintMessage
} from '@src/api/Complaints/models';
import { IDeleteMessage, ISocketMessage } from '@src/api/Dialogs/models';
import { MessageEvents } from '@src/api/SocketApi/models';
import { useAuth } from '@providers/AuthProvider';

export interface IFilterPostsForm {
  users?: SelectOption<string>;
  types?: SelectOption<ComplaintType>;
  status?: SelectOption<ComplaintStatus>;
  sort: SelectOption<string, { key: string, value: string }>;
}

const COMPLAINTS_LABELS: Record<ComplaintsSort, Record<SortValue, string>> = {
  [ComplaintsSort.CREATED_AT]: {
    [SortValue.ASC]: ' ',
    [SortValue.DESC]: ' ',
  },
}

const sortOptions: SelectOption<string, { key: string, value: string }>[] = Object.values(ComplaintsSort).reduce((res, it) => {
    return [...res, ...Object.values(SortValue).map(value => ({ value: `${it}_${value}`, label: COMPLAINTS_LABELS[it][value], meta: { key: it, value } }))];
  }, [] as SelectOption<string, { key: string, value: string }>[]);

export const useFilters = () => {
  const [activeComplaint, setActiveComplaint] = useState<IComplaint>();
  const [isLoading, setIsLoading] = useState(true);
  const [complaints, setComplaints] = useState<IComplaint[]>([]);
  const { user } = useAuth();

  const form = useFormik<IFilterPostsForm>({
    initialValues: {
      sort: sortOptions[0],
    },

    onSubmit: () => {}
  })

  const usersRes = useUsers();

  useEffect(() => {
    usersRes.getUsers();
  }, []);

  useEffect(() => {
    getComplaints()
  }, [form.values.users, form.values.status, form.values.sort, form.values.types]);

  useMessageCallBack((message: string) => {
    const parsedMessage = JSON.parse(message) as IComplaintMessage;

    if (parsedMessage.event === MessageEvents.COMPLAINT_MESSAGE) {
      setComplaints(prev => prev.map(it => {
        if (it.id === parsedMessage.complaintId && user?.id !== parsedMessage.user.id) {
          if (activeComplaint?.id !== parsedMessage.complaintId) {
            it.unreadableMessages += 1;
          }
        }
        return { ...it };
      }))
    }
  }, [complaints, user]);

  const getComplaints = async () => {
    try {
      setIsLoading(true)
      const { users, status, sort, types } = form.values;
      const res = await ComplaintsApi.fetchComplaints({
        user: users?.value,
        status: status?.value,
        type: types?.value,
        sort: sort.meta?.key,
        sortValue: sort.meta?.value,
      });
      setComplaints(res.data);
    } catch (e) {
      toastError(e)
    } finally {
      setIsLoading(false)
    }
  }

  const usersOptions: SelectOption<string>[] = useMemo(() => {
    return usersRes.users.map(it => ({ value: it.id, label: it.email, customize: { avatarSrc: it.avatar, avatarFirstName: it.firstName, avatarLastName: it.lastName } }))
  }, [usersRes.users]);

  const statusOptions: SelectOption<ComplaintStatus>[] = useMemo(() => {
    return Object.values(ComplaintStatus).map(it => ({ value: it, label: it }))
  }, []);

  const typesOptions: SelectOption<ComplaintType>[] = useMemo(() => {
    return Object.values(ComplaintType).map(it => ({ value: it, label: COMPLAINT_TYPE_LABELS[it] }))
  }, []);

  const createSelectChangeHandler = (field: string) => (value: CmSelectValue) => {
    form.setFieldValue(field, value);
  }

  const reset = () => {
    form.resetForm();
  }

  const onSetActiveComplaint = (complaint?: IComplaint) => {
    if (complaint) {
      setComplaints(prev => prev.map(it => it.id === complaint.id ? ({ ...it, unreadableMessages: 0 }) : it))
    }
    setActiveComplaint(complaint);
  }

  return {
    form,
    activeComplaint,
    setActiveComplaint: onSetActiveComplaint,
    usersOptions,
    typesOptions,
    usersRes,
    sortOptions,
    complaints,
    statusOptions,
    createSelectChangeHandler,
    isLoading,
    getComplaints,
    reset,
  }
}
