import { instance } from '@src/api/base';
import { IGetPostsParams, IPost, IPostBanDTO, IPostDetail } from '@src/api/Posts/models';

export class PostsApi {
  static fetchPosts(params?: IGetPostsParams) {
    return instance.get<IPost[]>('/admin/posts', {
      params,
    });
  }

  static fetchPostDetail(id: string) {
    return instance.get<IPostDetail>(`/admin/posts/${id}`);
  }

  static banPost({ id, ...dto }: IPostBanDTO) {
    return instance.post(`/admin/posts/ban/${id}`, dto);
  }

  static unbanPost(id: string) {
    return instance.post(`/admin/posts/unban/${id}`);
  }

  static deletePost(id: string) {
    return instance.delete(`/admin/posts/${id}`);
  }
}
