import { FC } from 'react';
import * as ST from './styled';
import { Avatar, StatusComplaint, TypeComplaint } from '@src/components';
import { IComplaint } from '@src/api/Complaints/models';

interface IProps {
  complaint: IComplaint;
  onClick: () => void;
}

export const ComplaintItem: FC<IProps> = ({ complaint, onClick }) => {
  return (
    <ST.Wrapper onClick={onClick}>
      <ST.Header>
        <ST.Title>
          # {complaint.id}
          {complaint.unreadableMessages && <ST.MessagesCount>{complaint.unreadableMessages}</ST.MessagesCount>}
        </ST.Title>
        <ST.Info>
          <TypeComplaint type={complaint.type} />
          <StatusComplaint status={complaint.status} />
        </ST.Info>
      </ST.Header>
      <ST.Info>
        <Avatar
          firstName={complaint.author.firstName}
          lastName={complaint.author.lastName}
          src={complaint.author.avatar}
          userId={complaint.author.id}
        />
        {complaint.author.lastName} {complaint.author.firstName} ({complaint.author.email})
      </ST.Info>
      <ST.Description>{complaint.text}</ST.Description>
    </ST.Wrapper>
  );
}
