import { FC, useEffect, useState } from 'react';
import * as ST from './styled';
import {
  AppLoader,
  Avatar,
  Button,
  ButtonVariant,
  Comment,
  InfoItem,
  Input,
  SideModal,
  Status,
  TextShorter,
  VideoPlayer
} from '@src/components';
import { UserStatus } from '@src/api/Users/models';
import { useModalControls } from '@src/hooks';
import { Dialog, DialogTypes } from '@components/Dialog';
import { toastError, toastSuccess } from '@src/utils/toast';
import { IPostDetail } from '@src/api/Posts/models';
import { PostsApi } from '@src/api/Posts/PostsApi';
import { Tag } from '@components/Tag';
import { getTime } from '@src/utils/date';
import { Icon, IconSize } from '@components/Icon';
import { useAuth } from '@providers/AuthProvider';
import { RoleType } from '@src/api/Roles/models';

interface IProps {
  postId: string;
  onClose: () => void;
  refetch?: () => void;
}

export const PostModal: FC<IProps> = ({onClose, refetch, postId }) => {
  const { user } = useAuth();
  const [post, setPost] = useState<IPostDetail>();
  const [isLoading, setIsLoading] = useState(false);
  const banModal = useModalControls();
  const deleteModal = useModalControls();
  const [banReason, setBanReason] = useState('');

  const isAdmin = user?.roles.map(it => it.value).includes(RoleType.ADMIN) || false;

  const isActive = post?.status === UserStatus.ACTIVE;

  const fetchPost = async () => {
    try {
      const res = await PostsApi.fetchPostDetail(postId);
      setPost(res.data)
    } catch (e) {
      toastError(e);
    }
  }

  const handleBlockUnBlock = async () => {
    try {
      if (post) {
        setIsLoading(true);
        if (isActive) {
          const res = await PostsApi.banPost({ id: post.id, text: banReason });
          toastSuccess(res.data.message);
        } else {
          const res = await PostsApi.unbanPost(post.id)
          toastSuccess(res.data.message);
        }
        refetch?.()
        banModal.close();
        onClose();
      }
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  const handleDelete = async () => {
    try {
      if (post) {
        setIsLoading(true);
        const res = await PostsApi.deletePost(postId);
        toastSuccess(res.data.message);

        refetch?.()
        deleteModal.close();
        onClose();
      }
    } catch (e) {
      toastError(e);
    } finally {
      setIsLoading(false);
    }
  }

  useEffect(() => {
    fetchPost();
  }, [postId]);

  return (
    <SideModal
      titleText={post && (
        <ST.Header>
          <ST.Title>
            <TextShorter tooltip>
              {post.title} ({post.id})
            </TextShorter>
          </ST.Title>
          <Status status={post.status} />
          <ST.InfoItem>
            <Icon type="eye" size={IconSize.LG} />
            {post.views}
          </ST.InfoItem>
          <ST.InfoItem>
            <Icon type="star-filled" size={IconSize.LG} />
            {post.favoriteCount}
          </ST.InfoItem>
          <ST.InfoItem>
            <Icon type="message" size={IconSize.LG} />
            {post.commentsCount}
          </ST.InfoItem>
        </ST.Header>
      )}
      onClose={onClose}
      closeOnDocumentClick={false}
    >
      <ST.Wrapper>
        {post ? (
          <>
            <InfoItem
              text="ID:"
              info={(
                <>
                  {post.id}
                </>
              )}
            />
            <InfoItem
              text=":"
              info={(
                <>
                  {post.title}
                </>
              )}
            />
            <InfoItem
              text=":"
              info={(
                <>
                  <Avatar
                    firstName={post.user.firstName}
                    lastName={post.user.lastName}
                    src={post.user.avatar}
                  />
                  {post.user.lastName} {post.user.firstName} ({post.user.email})
                </>
              )}
            />
            <InfoItem
              text=":"
              info={getTime(post?.createdAt)}
            />
            <InfoItem
              text=":"
              info={getTime(post?.updatedAt)}
            />
            <InfoItem
              text=":"
              info={(
                <ST.Info>
                  {post.tags.map(it => <Tag key={it.id} tag={it} />)}
                </ST.Info>
              )}
            />
            <InfoItem
              text=" :"
              info={(
                <ST.Description>
                  {post.shortDescription}
                </ST.Description>
              )}
            />
            <InfoItem
              text=":"
              info={(
                <ST.Description>
                  {post.description}
                </ST.Description>
              )}
            />
            {post.banReason && (
              <InfoItem
                text=" :"
                info={(
                  <ST.BanInfoWrapper>
                    {post.banReason.text}
                  </ST.BanInfoWrapper>
                )}
              />
            )}
            {post.banReason && (
              <InfoItem
                text=" :"
                info={(
                  <>
                    <Avatar
                      firstName={post.banReason.user.firstName}
                      lastName={post.banReason.user.lastName}
                      src={post.banReason.user.avatar}
                    />
                    {post.banReason.user.lastName} {post.banReason.user.firstName} ({post.banReason.user.email})
                  </>
                )}
              />
            )}
            <VideoPlayer
              url={post.videoUrl}
            />
            <ST.Comments>
              <ST.CommentsText>
                
              </ST.CommentsText>
              {post.comments.map(it => <Comment key={it.id} comment={it} />)}
            </ST.Comments>
            <ST.Footer>
              {[UserStatus.BANNED, UserStatus.DELETED].includes(post.status) && isAdmin && (
                <Button
                  fullWidth
                  onClick={deleteModal.open}
                  variant={ButtonVariant.DANGER}
                  text=""
                />
              )}
              <Button
                fullWidth
                onClick={banModal.open}
                variant={isActive ? ButtonVariant.DANGER : ButtonVariant.FILLED}
                text={isActive ? '' : ''}
              />
            </ST.Footer>
          </>
        ) : (
          <AppLoader />
        )}
      </ST.Wrapper>
      {banModal.isOpen && (
        <Dialog
          type={isActive ? DialogTypes.WARNING : DialogTypes.ANSWER}
          closeHandler={banModal.close}
          onConfirm={handleBlockUnBlock}
          actions={{
            cancel: {
              text: '',
              onClick: banModal.close,
            },
            submit: {
              isLoading,
              text: isActive ? '' : '',
            }
          }}
        >
          <ST.BanWrapper>
            <ST.BanText>
                 {isActive ? '' : ''}  "{post?.title}"?
            </ST.BanText>
            {isActive && <Input value={banReason} onChange={e => setBanReason(e.target.value)} placeholder=" " />}
          </ST.BanWrapper>
        </Dialog>
      )}
      {deleteModal.isOpen && (
        <Dialog
          type={DialogTypes.WARNING}
          closeHandler={deleteModal.close}
          onConfirm={handleDelete}
          actions={{
            cancel: {
              text: '',
              onClick: deleteModal.close,
            },
            submit: {
              isLoading,
              text: '',
            }
          }}
        >
               "{post?.title}"?
        </Dialog>
      )}
    </SideModal>
  )
}
